<template>
  <article class="portfolio-card">
    <div class="portfolio-card__inner">
      <div class="portfolio-card__img"></div>
      <div class="portfolio-card__content">
        <div class="portfolio-tabs">
          <ul class="portfolio-tabs__list list-reset">
            <li class="portfolio-tabs__item" v-for="(tab, index) in tabs" :key="index">
              <button class="portfolio-tabs__btn main-btn btn-reset"
                      @click="activeTab = index" :class="{ 'portfolio-tabs__btn--active': currentTab === tab }">
                {{ tab.name }}
              </button>
            </li>
          </ul>

          <div class="portfolio-tabs__content" v-for="(tab, index) in tabs" :key="index" v-show="activeTab === index">
            <ul class="portfolio-images list-reset">
              <li class="portfolio-images__item" v-for="img of 10">
                <div class="portfolio-images__img"></div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </article>
</template>

<script>

export default {
  name: "PortfolioCardNew",
  data() {
    return {
      activeTab: 0,
      tabs: [
        {name: "Desktop"},
        {name: "Tablet"},
        {name: "Mobile"},
      ],
    };
  },

  computed: {
    currentTab() {
      return this.tabs[this.activeTab];
    },
  },
}
</script>

<style lang="scss" scoped>
.portfolio-card {
  background-color: $second-color;
  padding: rem(8);

  &__inner {
    display: flex;
    column-gap: rem(20);
  }

  &__content {
    width: 100%;
  }

  &__img {
    border-radius: 6px;
    width: rem(117);
    height: rem(119);
    background-color: #6A6A6A;
    flex-shrink: 0;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      right: rem(-10);
      top: rem(5);
      bottom: rem(5);
      border-right: rem(2) solid rgba($bg-color, 0.8);
    }
  }
}

.portfolio-tabs {
  width: 100%;

  &__list {
    display: flex;
    column-gap: rem(12);
    width: 100%;
    margin-bottom: rem(15);
  }

  &__item {
    flex-grow: 1;
  }

  &__btn {
    padding: 0.7rem 3rem;
    font-size: rem(16);
    width: 100%;

    &--active {
      border-color: $link-color;
    }
  }

  &__content {
    overflow: auto;
    padding-bottom: rem(15);
  }
}

.portfolio-images {
  display: flex;
  justify-content: space-between;
  column-gap: rem(10);

  &__item {
    flex: 0 0 rem(60);
  }

  &__img {
    width: 100%;
    height: rem(60);
    background-color: #5a6181;
  }
}
</style>
