<template>
  <section class="portfolio">
    <div class="container">

      <ul class="portfolio__list list-reset" v-if="!loading">
        <li class="portfolio__item" v-for="post of pagedPosts" :key="post.id">
          <portfolio-card-new/>
        </li>
      </ul>

      <pre-loader v-else/>
    </div>

    <v-pagination v-model="currentPage" :length="pageCount" rounded="circle"></v-pagination>
  </section>
</template>

<script>
import PortfolioCard from "@/components/PortfolioCard";
import PreLoader from "@/components/PreLoader";
import PortfolioCardNew from "@/components/PortfolioCardNew";

export default {
  name: "PortfolioPage",
  components: {PortfolioCardNew, PreLoader, PortfolioCard},

  data: () => ({}),

  mounted() {
    this.$store.dispatch('FETCH_POSTS');
  },

  computed: {
    pagedPosts() {
      return this.$store.getters['pagedPosts'];
    },
    loading() {
      return this.$store.state.loading;
    },
    currentPage: {
      get() {
        return this.$store.state.currentPage;
      },
      set(page) {
        this.$store.state.currentPage = page;
      }
    },
    pageCount() {
      return Math.ceil(this.$store.state.posts.length / this.$store.state.postsPerPage);
    }
  },
}
</script>

<style lang="scss">
.portfolio {
  padding: rem(50) 0;

  //&__list {
  //  display: grid;
  //  grid-template-columns: repeat(3, 1fr);
  //  gap: rem(20);
  //}

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: rem(30) rem(15);
    margin-bottom: rem(30);
  }

  .masonry-wall {
    margin-bottom: rem(50);
  }

  .v-btn--size-default {
    font-size: rem(18);
  }
}


</style>
